import Vue from 'vue'
import Vuex from 'vuex'
import http from '../http/http'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isShow:false,
    count:0,
  },
  getters: {
  },
  mutations: {
    show(state){
      state.isShow=true
    },
    close(state){
      state.isShow=false
    },
    recount(state,res){
      state.count=res
    },
    clearCount(state){
      state.count=0 
    },
    reduceCount(state){
      state.count--
    }
  },
  actions: {
  async getCount(context){
   let res =  await http.get(`/message/count?accesstoken=${sessionStorage.getItem('token')}`)
    context.commit('recount',res.data)
    }
  },
  modules: {
  }
})
