<template>
    <div class="all-p" v-if="isShow">
        <div class="release">
            <h3>发布内容</h3>
            <el-form :model="ruleForm" :rules="rules" label-width="100px" ref=ruleForm>
                <el-form-item label="标题" prop="name">
                    <el-input v-model="ruleForm.name"></el-input>
                </el-form-item>
                <el-form-item label="类型" prop="region">
                    <el-select v-model="ruleForm.region" placeholder="请选择类型">
                        <el-option label="分享" value="share"></el-option>
                        <el-option label="问答" value="ask"></el-option>
                        <el-option label="招聘" value="job"></el-option>
                        <el-option label="客户端测试" value="dev"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="内容" prop="text">
                    <el-input type="textarea" v-model="ruleForm.text"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="submitForm('ruleForm')">立即发布</el-button>
                    <el-button type="danger" @click="cancel">取消</el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>

</template>
<script>
export default {
    data() {
        return {
            ruleForm: {
                name: '',
                region: '',
                text: '',
            },
            rules: {
                name: [
                    { required: true, message: '请输入标题', trigger: 'blur' },
                ],
                region: [
                    { required: true, message: '请选择类型', trigger: 'change' }
                ],
                text: [
                    { required: true, message: '请输入内容', trigger: 'change' }
                ]
            }
        };
    },
    computed: {
        isShow() {
            return this.$store.state.isShow
        }
    },
    methods: {
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$http({
                        method: 'POST',//添加
                        url: '/topics',
                        data: {
                            accesstoken:sessionStorage.getItem('token'),
                            title: this.ruleForm.name,
                            tab:this.ruleForm.region,
                            content:this.ruleForm.text
                        }
                    }).then(Response => {
                      
                        sessionStorage.setItem('topicId',Response.topic_id)
                        console.log(Response.topic_id)
                        history.go(0)
                    }).catch(res => {
                        alert(res.response.data.error_msg)
                    })
                    this.$store.commit('close')
                    this.ruleForm.name = ''
                    this.ruleForm.region = ''
                    this.ruleForm.text = ''
                } else {
                    console.log('error submit!!');
                    return false;
                }
            })
        },
        cancel() {
            this.$store.commit('close')
            this.ruleForm.name = ''
            this.ruleForm.region = ''
            this.ruleForm.text = ''
        }
    },

}
</script>
<style>
.all-p {
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.8);
    position: absolute;
    overflow: hidden;

}

.release {
    user-select: none;
    padding-top: 50px;
    width: 600px;
    position: absolute;
    left: 50%;
    margin-left: -450px;
    top: 162px;
    background-color: #fff;
    padding-right: 80px;
    padding-bottom: 50px;
}

.release h3 {
    text-align: center;
    margin-bottom: 30px;
}
</style>