import Vue from 'vue'
import VueRouter from 'vue-router'
import Cnode from '../views/CnodeView.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/login',
    component: () => import('../views/LoginView.vue')
  },
  {
    path: '/',
    component: Cnode,
    children: [
      {
        path: '',
        component: () => import('../views/MainView.vue'),
        children: [
          {
            path: '',
            component: () => import('../views/MainCaregory.vue')
          },
          {
            path: '/:tab',
            component: () => import('../views/MainCaregory.vue'),
            props: true,
          }
        ]
      },
      {
        path:'/topic/:topicId',
        name:'details',
        component: () => import('../views/DetailsView.vue'),
        props:true
      },
      {
        path:'/user/:userName',
        name:'userDetails',
        component: () => import('../views/UserDetails.vue'),
        props:true
      },
      {
        path:'/topic_collect/collect',
        name:'collect',
        component: () => import('../views/FavorView.vue'),
      },
      {
        path:'/message/Unreadmessage',
        name:'message',
        component: () => import('../views/MessageView.vue'),
      }
    ]
  },

]

const router = new VueRouter({
  mode: 'hash',
  routes
})

router.beforeEach((to, from, next) => {
  const loginIn = sessionStorage.getItem('token')
  if (loginIn || to.path === '/login') {
    next()
  } else {
    next('/login')
  }
})

export default router
